export const SearchBtn = (props) => {
  return (
    <Link to={`/search/${props.searchValue}`}>
      <button
        type="button"
        className="btn btn-light"
        style={{
          backgroundColor: "orange",
          color: "white",
          border: "none",
          height: "40px",
          padding: "0px 15px",
          fontSize: "16px",
          borderRadius: "0px",
        }}
        disabled={true}
        ref={props.disableRef}
      >
        <SearchOutlinedIcon />
      </button>
    </Link>
  );
};

