import React from "react";
import { debounce } from "lodash";

import App from "./App.js";
import Watch from "./Watch.js";
import CardInitial from "./CardInitial.js";

export const Hover = (props) => {
  const { item } = props;

  const [hover, setHover] = React.useState(false);
  const [delayMouseEntered, setDelayMouseEntered] = React.useState(null);
  const [delayMouseExit, setDelayMouseExit] = React.useState(null);

  // function debounce(func, interval) {
  //   var timeout;
  //   return function () {
  //     var context = this,
  //       args = arguments;
  //     var later = function () {
  //       timeout = null;
  //       func.apply(context, args);
  //     };
  //     clearTimeout(timeout);
  //     timeout = setTimeout(later, interval || 200);
  //   };
  // }

  const onMouseEnter = () => {
    clearTimeout(delayMouseExit);
    setDelayMouseEntered(
      setTimeout(() => {
        setHover(true);
      }, 500)
    );
  };

  const onMouseLeave = () => {
    clearTimeout(delayMouseEntered);
    setDelayMouseExit(
      setTimeout(() => {
        setHover(false);
      }, 500)
    );
  };

  return (
    <div
      id="hoverId"
      style={{
        //display: "flex",
        // justifyContent: "center",
        // alignItems: "center",
        position: "relative",
        height: "100%",
      }}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      {hover ? (
        <Watch style={{ position: "relative" }} item={item} />
      ) : (
        <CardInitial
          title={item.name}
          poster={item.image}
          urlname={item.urlname}
        />
      )}
    </div>
  );
};

export default Hover;
