import React from "react";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";

import "./App.css";
import Flickhub, { SearchItem } from "./Flickhub";
import Feedback from "./Feedback";
import About, { Contact } from "./About";
import flickhub from "./assets/images/logo3.jpg";
import Filter2 from "./Filter2";
import InfoPage from "./InfoPage";
import Shimmer from "./Shimmer";
import MobileHover from "./mobile/MobileHover";
import Hover from "./Hover";
import { responseObj } from "./utils/network";
import { useMediaQuery } from "react-responsive";
import MobileSpinner from "./mobile/MobileSpinner";
import PageNotFound from "./PageNotFound";
import Faq from "./Faq";

import CircularProgress from "@material-ui/core/CircularProgress";

export const SearchScreen = (props) => {
  const [respObj, setRespObj] = React.useState(null);
  const [notFound, setNotFound] = React.useState(false);

  // Link to server for making requests
  React.useEffect(() => {
    fetch(`https://flickhub.in/submit2/${props.match.params.searchString}`, {
      method: "POST",
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
      },
      mode: "cors",
      body: JSON.stringify({ mv_name: props.match.params.searchString }),
    })
      .then((response) => response.json())
      .then((response) => {
        setRespObj(response);
        response.data.length === 0 ? setNotFound(true) : setNotFound(false);
        // response === null ? console.log("No results") : console.log("Success!");
        // console.log("Response", response);
      })
      .catch((error) => console.log("error", error));
  }, [props.match.params.searchString]);

  return (
    <div>
      {!notFound ? (
        <div
          style={{
            marginTop: "100px",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          {respObj ? (
            <div>
              <SearchItem
                searchFor={props.match.params.searchString}
                respObj={respObj.data}
              />
            </div>
          ) : (
            <div
            // style={{
            //   position: "absolute",
            //   top: "50%",
            //   left: "50%",
            //   transform: "translate(-50%,-50%)",
            // }}
            >
              <MobileSpinner />
            </div>
          )}
        </div>
      ) : (
        <PageNotFound />
      )}
    </div>
  );
};

export const Info = (props) => {
  const [respObj, setRespObj] = React.useState(null);
  const mobile = useMediaQuery({ minWidth: 850 });

  console.log("id", props.match.params.id);
  React.useEffect(() => {
    fetch(`https://flickhub.in/title/${props.match.params.id}`, {
      method: "POST",
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((response) => {
        setRespObj(response);
        console.log(response);
      })
      .catch((error) => console.log("error", error));
  }, []);

  return (
    <div>
      {respObj ? (
        <InfoPage item={respObj.data[0]} />
      ) : (
        <div
          style={{
            height: "100vh",
            width: "100vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {mobile ? <Shimmer /> : <MobileSpinner />}
        </div>
      )}
    </div>
  );
};

function Routers(props) {
  const [search, setSearch] = React.useState(false);
  const [find, setFind] = React.useState("marvel");
  const [respObj, setRespObj] = React.useState(null);
  const [navBarColor, setNavBarColor] = React.useState("transparent");
  const [navBarFontColor, setNavBarFontColor] = React.useState("white");
  const [navBarShadow, setNavBarShadow] = React.useState("none");

  document.addEventListener("scroll", () => {
    if (window.scrollY < 100) {
      setNavBarColor("transparent");
      setNavBarFontColor("white");
      setNavBarShadow("none");
    } else {
      setNavBarColor("rgba(255,165,0,0.7)");
      setNavBarFontColor("black");
      setNavBarShadow(
        "rgba(0, 0, 0, 0.16) 0px 2px 5px 0px, rgba(0, 0, 0, 0.12) 0px 2px 10px 0px"
      );
    }
  });

  const aboutRef = React.useRef();

  const searchBar = (
    <input
      type="text"
      placeholder="Search"
      autoFocus={true}
      style={{
        transition: "all 0.5s ease",
        width: "0px",
        background: "rgba(50,50,50)",
        border: "none",
        borderRadius: "15px",
        padding: "10px",
        color: "white",
        marginLeft: "550px",
      }}
      onFocus={(e) => {
        e.target.style.width = "300px";
      }}
      onChange={(e) => {
        onChange(e);
        console.log("yaha bhi");
      }}
    />
  );

  const onChange = (e) => {
    setFind(e.target.value);
  };

  return (
    <Router>
      <ul
        style={{
          display: "flex",
          minWidth: "100vw",
          top: "0",
          left: "0",
          position: "fixed",
          zIndex: "2",
          padding: "3px 0px 3px 0px",
          backgroundColor: navBarColor,
          transition: "0.5s all ease",
          boxShadow: navBarShadow,
        }}
      >
        <li id="leftBtn">
          <Link to="/">
            <div style={{ display: "flex", transition: "all 1s ease" }}>
              <img
                alt=""
                src={flickhub}
                height="45px"
                width="45px"
                style={{ padding: "5px", marginLeft: "20px" }}
              />
            </div>
          </Link>
        </li>
        <li id="leftBtn" style={{ color: "white" }}></li>
        <div id="navRight">
          <li>
            <Link to="/filter">
              <button
                style={{
                  outline: "none",
                  textDecoration: "none",
                  outline: "none",
                  border: "none",
                  color: navBarFontColor,
                  fontWeight: "bolder",
                }}
                className="btn btn-link"
              >
                Filtered Search
              </button>
            </Link>
          </li>
          <li>
            <Link to="/faq">
              <button
                style={{
                  outline: "none",
                  textDecoration: "none",
                  outline: "none",
                  border: "none",
                  color: navBarFontColor,
                  fontWeight: "bolder",
                }}
                className="btn btn-link"
              >
                FAQs
              </button>
            </Link>
          </li>
          <li>
            <Link to="/about">
              <button
                style={{
                  outline: "none",
                  textDecoration: "none",
                  outline: "none",
                  border: "none",
                  color: navBarFontColor,
                  fontWeight: "bolder",
                }}
                className="btn btn-link"
              >
                About
              </button>
            </Link>
          </li>
          <li>
            <Link to="/feedback">
              <button
                style={{
                  outline: "none",
                  textDecoration: "none",
                  outline: "none",
                  border: "none",
                  color: navBarFontColor,
                  fontWeight: "bolder",
                }}
                className="btn btn-link"
              >
                Feedback
              </button>
            </Link>
          </li>
        </div>
      </ul>

      <Switch>
        <Route path="/feedback">
          <Feedback />
        </Route>
        <Route path="/filter">
          <Filter2 />
        </Route>
        <Route exact path="/about">
          <About />
        </Route>
        <Route exact path="/faq">
          <Faq />
        </Route>
        <Route path="/search/:searchString" component={SearchScreen} />
        <Route path="/title/:id" component={Info} />

        <Route path="/">
          <Flickhub />
        </Route>
      </Switch>
    </Router>
  );
}

export default Routers;
