import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { debounce } from "lodash";
import { SearchBtn } from "./../Flickhub";

const SearchBar = () => {
  const [searchValue, setSearchValue] = useState("");
  const [suggestions, setSuggestions] = useState([]);

  const searchStringRef = React.useRef();
  const disableRef = React.useRef();

  const history = useHistory();

  useEffect(() => {
    if (searchValue !== "") {
      fetch(`https://flickhub.in/search/${searchValue}`, {
        method: "POST",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
        // mode: "cors",
      })
        .then((response) => response.json())
        // .then((response) => console.log("Random ", response))
        .then((response) => {
          setSuggestions(response.data);
        })
        .catch((error) => setSuggestions([]));
    } else setSuggestions([]);
  }, [searchValue]);

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexWrap: "wrap",
        width: "100vw",
        marginTop: "130px",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          width: "100vw",
          //margin: "0px 20px 100px 20px",
        }}
      >
        <div
          style={{
            width: "650px",
            height: "40px",
            border: "none",
            fontSize: "16px",
            color: "#6e6d7a",
          }}
        >
          <Autocomplete
            style={{ padding: "0", margin: "0" }}
            autoComplete={true}
            filterSelectedOptions={true}
            freeSolo
            id="free-solo-2-demo"
            disableClearable
            // onInputChange={(event) => {
            //   console.log(event.target.value);
            //   if (event.target.value === "") {
            //     setSuggestions([]);
            //   } else {
            //     event.persist();
            //     const debounceFunc = debounce(() => {
            //       setSearchValue(event.target.value);
            //     }, 500);
            //     debounceFunc();
            //   }
            //   event.target.value !== ""
            //     ? (disableRef.current.disabled = false)
            //     : (disableRef.current.disabled = true);
            // }}
            onChange={(object, value) => {
              console.log(object);
              setSearchValue(value);
            }}
            autoSelect={true}
            options={suggestions.map((option) => option)}
            renderInput={(params) => (
              <TextField
                {...params}
                placeholder="Search with a keyword"
                margin="normal"
                InputProps={{
                  ...params.InputProps,
                  type: "search",
                  disableUnderline: true,
                }}
                style={{
                  color: "white",
                  backgroundColor: "white",
                  outline: "none",
                  padding: "5px 15px 5px 15px",
                  margin: "0",
                  height: "40px",
                }}
                value={searchValue}
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    //window.location = `/search/${searchValue}`;
                    if (searchValue !== "")
                      history.push(`/search/${searchValue}`);
                  }
                }}
                onChange={(event) => {
                  //console.log("yahan aaya");
                  // setSearchValue(e.target.value);
                  // if (e.target.value === "") {
                  //   setSuggestions([]);
                  // }
                  // e.target.value !== ""
                  //   ? (disableRef.current.disabled = false)
                  //   : (disableRef.current.disabled = true);
                  console.log(event.target.value);
                  if (event.target.value === "") {
                    setSuggestions([]);
                  } else {
                    event.persist();
                    const debounceFunc = debounce(() => {
                      setSearchValue(event.target.value);
                    }, 500);
                    debounceFunc();
                  }
                  event.target.value !== ""
                    ? (disableRef.current.disabled = false)
                    : (disableRef.current.disabled = true);
                }}
                autoFocus={false}
                ref={searchStringRef}
                fullWidth={true}
              />
            )}
          />
        </div>
        <SearchBtn searchValue={searchValue} disableRef={disableRef} />
      </div>
    </div>
  );
};

export default SearchBar;
